@import "../../styles/utils";

.control {
  &:not(:last-child) {
    margin-right: 20px;

    @media (max-width: $break-point-phone) {
      margin-right: 0;
    }
  }

  &__input-wrapper {
    @extend %flex-row-center;

    padding: 12px 16px;

    border: 1px solid $color-grey;
    transition: border-color 300ms;
  }

  &--focused {
    border-color: #62b80d;
  }

  &__icon {
    width: 14px;
    height: 14px;
    margin-right: 10px;
  }

  &__input {
    max-height: 24px;
    line-height: 24px;

    &--time {
      max-width: 75px;
    }

    &--small {
      @media (max-width: $break-point-tablet-lg) {
        max-width: 75px;
      }
    }
  }

  &__label {
    font-size: 14px;
    color: #626262;

    margin-bottom: 8px;
  }
}
